.formButton {
  height: 80px;
  width: 160px;
  border-radius: 8px;
  border: 2px solid var(--venitianRed);
  margin: 50px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.formButton:hover {
  box-shadow: 0px 0px 10px black;
  background-color: var(--venitianRed);
  cursor: pointer;
}

.formButton:active {
  box-shadow: 0px 0px 7px black;
}

.formText {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
  z-index: 10;
}

.formText:hover {
  color: var(--background-color);
}
